button {
    outline: none;
    border: 0;
    cursor: pointer;
}

.habit-count {
    display: inline-block;
    text-align: center;
    font-size: 2rem;
    width: 2.5rem;
    height: 2.5rem;
    line-height: 2.5rem;
    margin-left: 0.2em;
    margin-right: 0.5em;
    background-color: darkseagreen;
    color: white;
    border-radius: 50%;
}

.habit {
    font-size: 2.5rem;
    padding: 0.5em;
    list-style: none;
}

.habit-button {
    font-size: 2.5rem;
    margin: 0 0.1em;
    background-color: transparent;
}

.habit-button:hover {
    opacity: 0.8;
}

.habit-increase,
.habit-decrease {
    color: goldenrod;
}

.habit-delete {
    font-size: 2rem;
    color: darkred;
}

.nav-contanier {
    width: 100%;
    height: 50px;
    font-size: 30px;
    background-color: wheat;
    padding: 20px;
}

.nav-contanier * {
    margin-right: 3px;
}

.fa-leaf {
    color: green;
}

.nav-count {
    display: inline-block;
    text-align: center;
    font-size: 2rem;
    width: 2.5rem;
    height: 2.5rem;
    line-height: 2.5rem;
    margin-left: 0.2em;
    margin-right: 0.5em;
    background-color: darkseagreen;
    color: white;
    border-radius: 50%;
}

.add-form {
    width: 100%;
    height: 30px;
    display: flex;
    margin: 10px;
    margin-left: 30px;
}

.add-input {
    margin-right: 3px;
}

.add-button,
.resetBtn {
    background-color: darkseagreen;
    color: white;
}

.add-button:hover {
    background-color: wheat;
}

.resetBtn {
    font-size: large;
    margin-left: 50px;
}
